import React from 'react';
import Card from './Card';


export default function Cards(props) {

  return (
    <>
    {props.cities.map( (card) => (
      <Card
      key = {card.id}
      onClose = {() => alert(card.name)}
      name = {card.name} 
      max = {card.main.temp_max}
      min = {card.main.temp_min}
      img = {card.weather[0].icon}
      />
   ))}
   </>
  ) 

};